.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItem {
    padding: 10px;
}

.dialogsItem .active {
    background-color: #92b6ed;
}

.dialog {
    margin: 5px;
    border-radius: 3px;
}

.dialog a {
    text-decoration: none;
}

.messages {
    padding: 10px;
}

